import React, {Component} from 'react';
import Slider from 'react-slick';
import data from './data.json';
import workoutImage from './images/workout.jpg';
import foodImage from './images/food.jpg';

class Grid extends Component {

    constructor(props) {
        super(props);

        this.state = {
            activeSliderIndex: 0
        };

        this.settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            className: 'article-slider',
            afterChange: this.sliderChange.bind(this)
        };

        this.anotherSettings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 3
        }

        this.gridData = data.concat(data).concat(data).slice(0, 9);
        this.gridData.forEach((item) => {
            if (Math.random() > 0.5) {
                item.isImage = true;
            }
        })
    }

    sliderChange(newIndex) {
        this.setState({
            activeSliderIndex: newIndex
        });
    }

    render() {
        return (
            <div className="container">
                <h1 className="Section-Title">
                    Журнал
                </h1>
                <Slider {...this.settings}>
                    {
                        data.map((item, index) => <div key={index}><Slide data={item} /></div>)
                    }
                </Slider>
                <div className="row">
                    {data.map((item, index) => <SliderCard key={index} data={item} isActive={this.state.activeSliderIndex === index}/>)}
                </div>
                <h1 className="Section-Title">
                    Теги: <span className="Tag-List">Все</span>
                </h1>
                <div className="row">
                    {this.gridData.map((item, index) => {
                        return item.isImage ? (
                            <Card key={index} data={item} />
                        ) : (
                            <ImageCard key={index} data={item} />
                        )
                    })}
                </div>
                <h1 className="Section-Title">
                    Рецепты
                </h1>
                <Slider {...this.anotherSettings}>
                    {
                        this.gridData.map((item, index) => <div key={index}><FilledCard/></div>)
                    }
                </Slider>
                <h1 className="Section-Title">
                    Еще Статьи
                </h1>
                <div className="row">
                    {this.gridData.slice(0,6).map((item, index) => {
                        return item.isImage ? (
                            <Card key={index} data={item} />
                        ) : (
                            <ImageCard key={index} data={item} />
                        )
                    })}
                </div>
                <h1 className="Section-Title">
                    Комплексы
                </h1>
                <Slider {...this.anotherSettings}>
                    {
                        this.gridData.map((item, index) => <div key={index}><FilledCard workout/></div>)
                    }
                </Slider>

            </div>
        );
    }
}


const Slide = ({data}) => {
    return (
        <div className="Slide" style={{'backgroundImage': `url(${data.img})`}}>
            <div className="Slide__filter"></div>
            <div className="Slide__content">
                <a href="/away" className="Slide__tag Tag Tag--slide">
                    {data.tag}
                </a>
                <a href="/away" className="Slide__title">
                    {data.title}
                </a>
            </div>
        </div>
    );
};

const SliderCard = ({data, isActive}) => {
    return (
        <div className="SliderCard col-xs-3">
            {isActive && <div className="triangle"></div>}
            <div className="SliderCard__separator" />
            <div className="Tag Tag--card">
                {data.tag}
            </div>
            <div className="SliderCard__title">
                {data.title}
            </div>
            <div className="SliderCard__date">
                2 дня назад
            </div>
        </div>
    );
}

const Card = ({data}) => {
    return (
        <div className="Card col-xs-4">
            <div className="Card__border">
                <div className="Card__content">
                    <div className="Card__tag">
                    {data.tag}
                    </div>
                    <div className="Card__title">
                        {data.title}
                    </div>
                    <div className="Card__date">
                        2 дня назад
                    </div>
                </div>
             </div>
        </div>
    );
}

const FilledCard = (props) => {
    console.log(props);
    const isWorkout = props.workout;
    const backgroundImage = `url(${isWorkout ? workoutImage : foodImage})`
    const title = isWorkout ? 'Лучшие упражнения на свежем воздухе' : 'Сахар: где, что и сколько'
    return (
        <div className="FilledCard" style={{'backgroundImage': backgroundImage}}>
            <div className="FilledCard__filter"></div>
            <div className="FilledCard__border">
                <div className="FilledCard__content">
                    <div className="FilledCard__title-container">
                        <div className="FilledCard__title">
                            {title}
                        </div>
                    </div>
                    <div className="FilledCard__date">
                        2 дня назад
                    </div>
                </div>
             </div>
        </div>
    );
}

const ImageCard = ({data}) => {
    return (
        <div className="Card col-xs-4">
            <div className="Card__border">
                <div className="ImageCard__content">
                    <div className="Card__image">
                    </div>
                    <div className="ImageCard__text">
                        <div className="Card__tag Card__tag--purple">
                            {data.tag}
                        </div>
                        <div className="Card__title">
                            {data.title}
                        </div>
                        <div className="Card__date">
                            2 дня назад
                        </div>
                    </div>
                </div>
             </div>
        </div>
    );
}


export default Grid;


