import React from 'react';
import Social from './Social';
import YogaLogo from './yoga_white.svg';

const Header = () => {
    return (
        <div className="Header">
            <div className="row">
                <div className="col-xs-6 Header__item">
                    <img className="Header__logo" src={YogaLogo} alt="Yoga+Life Logo"/>
                </div>
                <div className="col-xs-6 Header__item">
                    <Social />
                </div>
            </div>
        </div>
    );
};

export default Header;


