import React from 'react';
import eventImg from './images/event.jpg';
import grassImg from './images/grass.jpg';
import practiceImg from './images/practice.jpg';

const MenuItems = [
    {
        item: 'Журнал',
        title: 'Yoga+life — журнал о здоровом образе жизни',
        sum: 'Мы знакомим читателей с основами йоги, которую понимаем очень широко — как возможность гармонично и радостно жить в этом мире.',
        image: grassImg
    },
    {
        item: 'Практика',
        title: 'Йога — Практика',
        sum: 'Практика, Ассаны, Комплексы, Спорт и все что с ними связано',
        image: practiceImg,
        href: '/practice'
    },
    {
        item: 'События',
        title: 'События из мира йоги и здорового образа жизни',
        sum: 'События из мира йоги и здорового образа жизни. События из мира йоги и здорового образа жизни',
        image: eventImg,
        href: '/events'
    }
];

class MainMenu extends React.Component {
    coverDOMNode = null;
    constructor(props) {
        super(props);
        this.state = {
            activeIndex: 0
        }
    }

    componentDidMount() {
        this.coverDOMNode = document.getElementById('Cover');
        this.setImage();
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.activeIndex !== this.state.activeIndex) {
            this.setImage();
        }
    }

    setImage() {
        this.coverDOMNode.style.backgroundImage = `url(${MenuItems[this.state.activeIndex].image})`;
    }

    handleMouseEnter(indexEnter) {
        this.setState({
            activeIndex: indexEnter
        })
    }

    render() {
        return (
                <div className="middle-xs container MainMenu">
                        <div className="col-xs-3">
                            <ul className="MainMenu__Items">
                                {MenuItems.map((item, index) => this.renderMenuItem(item, index))}
                            </ul>
                        </div>
                        <div className="col-xs-9">
                            <div className="MainMenu__ItemsSummury">
                                <h4>
                                    {MenuItems[this.state.activeIndex].title}
                                </h4>
                                <p>
                                    {MenuItems[this.state.activeIndex].sum}
                                </p>
                            </div>
                        </div>
                </div>
        );
    }

    renderMenuItem(item, index) {
        return (
            <li key={item.item} className={index === this.state.activeIndex ? 'active' : ''}>
                {
                    item.href ?
                    (
                        <a href={item.href} onMouseEnter={() => this.handleMouseEnter(index)}>{item.item}</a>
                    ) :
                    (
                        <a href="#magazine" onClick={this.props.scrollToContent} onMouseEnter={() => this.handleMouseEnter(index)}>{item.item}</a>
                    )
                }
            </li>
        );
    }
};

export default MainMenu;


